let $savedMessage = $('[data-role=saved-message]');

const saveRedmineLoading = () => {
  $savedMessage.html(Translator.trans('正在保存...')).show();
}

const saveRedmineSuccess = () => {
  $savedMessage.html(Translator.trans('已保存...')).show();
  setTimeout(() => {
    $savedMessage.hide();
  }, 3000);
}

const saveRedmineMessage = (message) => {
  savedMessage.html(Translator.trans(message)).show();
  setTimeout(() => {
    $savedMessage.hide();
  }, 3000);
}

export {
  saveRedmineLoading,
  saveRedmineSuccess,
  saveRedmineMessage
}